.App {
  max-width: 760px;
}

.logo-svg {
  background: white;
}

.intro {
  padding: 1em 0 1em 0;
  font-style: normal;
  font-weight: 800;
  font-size: 38px;
  line-height: 46px;
}

.profile,
.contact {
  padding-bottom: 1em;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 34px;
}

.contact {
  margin-right: 0.5em;
  font-weight: 600;
}

#dgis {
  color: #52b311;
}

#facebook {
  color: #1877f2;
}

#instagram {
  background: radial-gradient(
    circle at 23% 100%,
    #fed373 30%,
    #f15245 42%,
    #d92e7f 48%,
    #9b36b7 55%,
    #515ecf
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

#mail {
  color: #d44638;
}

#linkedin {
  color: #007bb6;
}

#github {
  color: #fafafa;
}

#goodreads {
  color: #b7ad98;
}

.goodreads-good {
  font-weight: 100;
}
